
.result-item{
    width:100%;
    padding: 0.5rem 0rem 0 0rem;
    border-radius:5px;
    margin-bottom: 0.8rem;
    background: white;
    border:1px solid rgba(0,0,0,0.1);
}
.result-item .item-wrap{
    width:100%;
    display:-webkit-box; display: -ms-flexbox; display: flex;
    box-orient:horizontal;-webkit-box-orient:horizontal; box-lines:single;-webkit-box-lines:single;

    -ms-flex-direction:row;

        flex-direction:row; -moz-flex-direction:row;
    -ms-flex-wrap:no-wrap;
        flex-wrap:no-wrap; -webkit-flex-wrap:no-wrap; -moz-flex-wrap:no-wrap;

    -ms-flex-pack:left;

        justify-content:left;
    box-pack:left;-webkit-box-pack:left;-moz-box-pack:left;
}
.result-item .item-wrap .left{
    display:block; position:relative;
    box-flex:0; -webkit-box-flex:0;
    -ms-flex-positive:0;
     flex-grow:0;
    -ms-flex-negative:0;
        flex-shrink:0;
    -ms-flex-preferred-size:10rem;
        flex-basis:10rem;
    width:10rem;
    margin-left: 0.5rem;
    overflow-x:hidden;
}
.result-item .item-wrap .left .thumb{
    /*position: absolute;*/
    width:100%; height:100%;
    /*top:0;bottom:0;*/
    overflow-x:hidden;

    display:-webkit-box; display: -ms-flexbox;
    display: flex;
    box-orient:vertical;-webkit-box-orient:vertical; box-lines:single;-webkit-box-lines:single;
    -ms-flex-align:center;
        align-items:center; -moz-align-items:center;
    box-align:center;-moz-box-align:center;-webkit-box-align:center;
    -ms-flex-direction:row;
        flex-direction:row; -moz-flex-direction:row;
    -ms-flex-wrap:no-wrap;
        flex-wrap:no-wrap; -webkit-flex-wrap:no-wrap; -moz-flex-wrap:no-wrap;
    -ms-flex-pack:center;
        justify-content:center;
    box-pack:center;-webkit-box-pack:center;-moz-box-pack:center;
    -ms-flex-line-pack: center;
        align-content: center;
}
.result-item .item-wrap .left .thumb img{
    display: block;
    height:10rem; width:auto;
    -ms-flex-item-align:center;
        -ms-grid-row-align:center;
        align-self:center;
   /* margin:0 auto;*/
}
.result-item .item-wrap .content-wrap{
    display: block;
    box-flex:1; -webkit-box-flex:1;
    -ms-flex-positive:0;
     flex-grow:0;
    -ms-flex-negative:1;
        flex-shrink:1;
    -ms-flex-preferred-size:auto;
        flex-basis:auto;
    padding:0 1rem;
}
.result-item .item-wrap .content-wrap p{
  padding:0; margin:0;
}
.result-item .item-wrap .content-wrap .c-name{
    display:-webkit-box;
    text-overflow: ellipsis;
    overflow:hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 1.6rem;
    font-size: 1.4rem;
    font-weight: bold;
}
.result-item .item-wrap .content-wrap .e-name{
    display:-webkit-box;
    text-overflow: ellipsis;
    overflow:hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    line-height: 1.6rem;
    font-size: 1.4rem;
     font-weight: bold;
}
.result-item .item-wrap .content-wrap .rigion{
    margin-top:0.4rem;
    line-height: 1.2rem; font-size: 1.2rem;
}
.result-item .item-wrap .content-wrap .type{
    margin-top:0.4rem;
    line-height: 1.4rem; font-size: 1.2rem;
}
.result-item .item-wrap .content-wrap .grape{
     margin-top:0.4rem;
    line-height: 1.4rem; font-size: 1.2rem;
}
.result-item .btn-wrap{
    width:100%; height:3rem;
    margin-top:1rem;
    border-top:1px solid rgba(0,0,0,0.2);
}
.result-item .btn-wrap>a{
    display: block; position: relative;
    float:left;
    width:50%; height:3rem; line-height: 3rem;
    font-size: 1.2rem;
    text-align: center;
}
.result-item .btn-wrap>a:nth-of-type(2):after{
    content: "";
    position: absolute;
    left:0;top:0;
    width:1px; height:100%;
    background:  rgba(0,0,0,0.2);
}



