
@import "../../common/css/reset.css"
@import "../../common/css/cusstyle.css"
.app{
  position: relative;
  width:100%;
}
.filter-wrap[data-v-c30b2bcc]{
  position:absolute;
  padding-top:1.8rem;
  left:0;right:0;top:0;bottom:0;
  font-size:1rem;
  /*background:red;*/
}
.filter-wrap .close-filter[data-v-c30b2bcc]{
  display: block;
  padding:0;
  position: absolute;
  right:1rem; top:1rem;
  width:3rem; height:3rem; line-height:3rem;
  font-size: 2rem;
  text-align: center;
  font-weight: 700;
  color:gray;
  z-index: 3;
}
.filter-wrap .select-wrap[data-v-c30b2bcc]{
  width:100%;
  padding-bottom:8rem;
}
.filter-wrap .bottom[data-v-c30b2bcc]{
  position: absolute;
  height:4rem;
  left:0;right:0;bottom:0;
  background: white;
}
.filter-wrap .bottom[data-v-c30b2bcc]:after{
  content: "";
  position:absolute;
  width:100%; height: 1px;
  top:-1px; left:0;
  transform:scaleY(0.5);
  -webkit-transform:scaleY(0.5);
  background: #E0E0E0;
}
.filter-wrap .bottom .btn[data-v-c30b2bcc]{
  position: relative;
  display: inline-block;

  width:50%; height:4rem; line-height: 4rem;
  text-align:center;
}
.filter-wrap .bottom .btn[data-v-c30b2bcc]:nth-of-type(2){
  margin-left: -10px;
}
.filter-wrap .bottom .btn[data-v-c30b2bcc]:nth-of-type(2):after{
  content: "";
  position: absolute;
  left: 0; top:0;
  height: 100%; width: 1px;
  transform:scaleX(0.5);
  -webkit-transform:scaleX(0.5);
  background: #E0E0E0;
}
.list-wrap[data-v-c30b2bcc]{
  width:100%;
  box-sizing: border-box;
  padding:1rem 1rem;
}
.list-wrap .result-list[data-v-c30b2bcc]{
  box-sizing: border-box;
  padding:0; margin:0;
 /* padding:1rem 0.5rem 3rem 0.5rem;*/
}
.list-wrap .no-result[data-v-c30b2bcc], .list-wrap .add-more[data-v-c30b2bcc]{
  width:100%; height:2rem; line-height: 2rem;
  font-size: 1.8rem; text-align: center;
}



