
.tabbar-wrap{
    position: fixed;
    top:0;left:0;
    width: 100%;
    z-index: 5;
}
.tabbar-wrap .tabbar{
    position:relative;
    width: 100%; max-width:640px;
    margin: 0 auto;
    color:white;
    background : white;
}
.tabbar-wrap .tabbar .filter-open{
  position: absolute;
  right:0.5rem; top:0.25rem;
  width:2.4rem; height:2.4rem; line-height: 2.4rem;
  border:5px solid rgba(0,0,0,0);
  font-size: 1.4rem;
  text-align: center;
  border-radius: 50%;
  background: blue;
  z-index: 3
}
.tabbar-wrap .tabbar .symbol-wrap{
  width:90%;min-height: 2.4rem;
  padding:0.5rem 0rem;
}
.tabbar-wrap .tabbar .symbol-wrap .symbol{
    display: inline-block;
    position: relative;
    height:2rem; line-height: 2rem;
    font-size: 1.2rem;
    text-align: center;
    padding:0rem 0.8rem;
    margin:0.5rem 0.25rem;
    border-radius:8px;
    background: red;
}
