
.filter-list{
  position: relative;
  width:100%;
  border-bottom:1.5rem solid rgba(0,0,0,0);/*blue;*/
  margin-bottom: 0.5rem;
}
.filter-list .express{
    position: absolute;
    display: inline-block;
    padding: 0.5rem 1rem;
    right:2rem; bottom:-3rem; line-height: 2rem;
    font-size: 1.4rem;
}
.filter-list .title{
  height:2rem; line-height: 2rem;
  font-size: 1.4rem;
  padding-left: 0.5rem;
  border-left:3px solid #B91C36;
}
.filter-list .tag-wrap{
  width:100%; max-width:100%;
  padding-left:0.2rem;

  display:-webkit-box; display: -ms-flexbox;
  display: flex;

  box-orient:horizontal;-webkit-box-orient:horizontal; box-lines:multiple;-webkit-box-lines:multiple;

  -ms-flex-direction:row;

      flex-direction:row; -moz-flex-direction:row;
  -ms-flex-wrap:wrap;
      flex-wrap:wrap; -webkit-flex-wrap:wrap; -moz-flex-wrap:wrap;

  -ms-flex-pack:left;

      justify-content:left;
  box-pack:left;-webkit-box-pack:left;-moz-box-pack:left;
}
.filter-list .tag-wrap .col{
    display: block;

    -webkit-box-flex:0;

        -ms-flex-positive:0;

            flex-grow:0;
    -ms-flex-negative:1;
        flex-shrink:1;
    -ms-flex-preferred-size:auto;
        flex-basis:auto;
    text-align: center;
    padding:0.5rem 0.5rem;
}
.filter-list .tag-wrap .col .tag{
  display: inline-block;
  box-sizing: border-box;
  width:100%;

  padding:0.5rem 1.2rem;
  border-radius:6px;
  overflow: hidden;
  color:#545454;
  background: white;
}
.filter-list .tag-wrap .col .tag span{
  display: block;
  width:100%; line-height: 2rem;font-size:1.2rem;
}
.filter-list .tag-wrap .col .tag.active{
    color:white;
    background: #B91C36;
}



